@import url('https://fonts.googleapis.com/css2?family=Koulen&display=swap');

.maincontainer-home{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.maincontainer-home .subcontainer{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}


.maincontainer-home .subcontainer.first{
    padding-top: 65px;
    background: url("../assets/spacebg.jpg") repeat 0 0;
    background-size: 2000px;
    /* filter: grayscale(); */
    display: flex;
    flex-direction: column;
    
}
.subcontainer.first .content1{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFAC41;
    font-family: 'Koulen', cursive;
    font-size: 100pt;
    font-weight: 900;
    /* -webkit-text-stroke: 1px #FFAC41; */
  
}
.subcontainer.first .subcontent1{
    color: #ffffff;
    font-weight: 400;
    margin-top: 20px;
}

.maincontainer-home .subcontainer.second{
    background-color: #000000;
}
.subcontainer .box{
    width: 100px;
    height: 100px;
    background-color: #FF1E56;
}